@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    font-family: 'Inter', system-ui, sans-serif;
    scroll-behavior: smooth;
  }
  
  body {
    @apply bg-gradient-to-b from-gray-50 to-white text-gray-900;
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}

@layer components {
  .glass {
    @apply bg-white/70 backdrop-blur-md border-b border-white/20;
  }

  .btn-primary {
    @apply px-6 py-3 bg-gradient-to-r from-blue-500 to-blue-600 text-white 
           rounded-full hover:from-blue-600 hover:to-blue-700 
           transition-all duration-300 shadow-lg hover:shadow-xl 
           transform hover:-translate-y-0.5 font-semibold;
  }

  .btn-secondary {
    @apply px-6 py-3 bg-white text-blue-600 border-2 border-blue-600 
           rounded-full hover:bg-blue-50 transition-all duration-300 
           shadow-md hover:shadow-lg transform hover:-translate-y-0.5 
           font-semibold;
  }
  
  .container {
    @apply max-w-7xl mx-auto px-4 sm:px-6 lg:px-8;
  }

  .card {
    @apply bg-white rounded-xl shadow-lg p-6 hover:shadow-xl 
           transition-all duration-300 transform hover:-translate-y-1 
           text-center border border-gray-100;
  }

  .section-title {
    @apply text-4xl sm:text-5xl font-bold text-transparent bg-clip-text 
           bg-gradient-to-r from-blue-600 to-blue-400;
  }

  .nav-link {
    @apply text-gray-700 hover:text-blue-600 transition-colors duration-200 
           relative after:absolute after:bottom-0 after:left-0 after:w-0 
           after:h-0.5 after:bg-blue-600 after:transition-all after:duration-300 
           hover:after:w-full;
  }
}

/* Custom animations */
@keyframes float {
  0% { transform: translateY(0px); }
  50% { transform: translateY(-10px); }
  100% { transform: translateY(0px); }
}

.animate-float {
  animation: float 3s ease-in-out infinite;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  @apply bg-gray-100;
}

::-webkit-scrollbar-thumb {
  @apply bg-primary-400 rounded-full hover:bg-primary-500 transition-colors duration-200;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
